body{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.div_container{
  border: 2px solid red;
  display: flex;
  
 
}
.main{
  border: 2px solid rgb(13, 26, 214);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
}

.container{
  border: 2px solid rgb(212, 0, 255);
  display: flex;
  
  flex-direction: column;
  width: 500px;
  height: 600px;
  margin: 30px;
}
.container img{
  width: 100%;
  border: 2px solid red;
  height: 50%;

}
.form_container{
  
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.form_input{
  display: flex;
  flex-direction: column;
}
input{
  margin: 20px;
  width: 500px;
}
.btn_sec{
 
  display: flex;
  justify-content: center;
  margin: 20px;
  
  
}
.subbtn{
  cursor: pointer;
  padding: 10px 30px;
  border: none;
  border-radius: 10px;
}
